// app/home/page.tsx
"use client";

import { useState, useEffect } from "react";
import { createClient } from "@/lib/supabase/client";
import { Feed } from "@/components/feed/feed";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertCircle, Users, Plus, Pencil, Sparkles } from "lucide-react";
import { Skeleton } from "@/components/ui/skeleton";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import {
  Tabs,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs";
import { Textarea } from "@/components/ui/textarea";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import type { User } from "@supabase/supabase-js";

interface Profile {
  id: string;
  display_name: string | null;
  avatar_url: string | null;
  bio: string | null;
}

export default function HomePage(): React.JSX.Element {
  const [user, setUser] = useState<User | null>(null);
  const [profile, setProfile] = useState<Profile | null>(null);
  const [followingUserIds, setFollowingUserIds] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [databaseError, setDatabaseError] = useState<string | null>(null);
  const [showFollowButtons, setShowFollowButtons] = useState(true);
  const [feedTab, setFeedTab] = useState<"for-you" | "following">("for-you");
  const [createPostDialogOpen, setCreatePostDialogOpen] = useState(false);
  const [postContent, setPostContent] = useState("");
  const [posting, setPosting] = useState(false);
  const [postError, setPostError] = useState<string | null>(null);
  const [characterCount, setCharacterCount] = useState(0);

  const supabase = createClient();

  useEffect(() => {
    void fetchUserData();
  }, []);

  async function fetchUserData(): Promise<void> {
    try {
      setIsLoading(true);
      
      const { data: { user: currentUser } } = await supabase.auth.getUser();
      setUser(currentUser);

      if (currentUser) {
        const { data: userProfile } = await supabase
          .from("profiles")
          .select("*")
          .eq("id", currentUser.id)
          .maybeSingle();
        setProfile(userProfile);

        const { data: follows } = await supabase
          .from("follows")
          .select("following_id")
          .eq("follower_id", currentUser.id);
        
        const followingIds = follows?.map(follow => follow.following_id) || [];
        setFollowingUserIds([...followingIds, currentUser.id]);

        const { error: profileError } = await supabase
          .from("profiles")
          .select("id")
          .limit(1);
        
        if (profileError?.message?.includes("Could not find the table")) {
          setDatabaseError("database_not_setup");
        }
      }
    } catch (error) {
      console.error("Error fetching user data:", error);
    } finally {
      setIsLoading(false);
    }
  }

  const handleFollowChange = (userId: string, isFollowing: boolean): void => {
    if (isFollowing) {
      setFollowingUserIds(prev => [...prev, userId]);
    } else {
      setFollowingUserIds(prev => prev.filter(id => id !== userId));
    }
  };

  const handleCreatePost = async (): Promise<void> => {
    if (!user || !postContent.trim()) return;

    setPosting(true);
    setPostError(null);

    try {
      const { error } = await supabase
        .from("posts")
        .insert({
          user_id: user.id,
          content: postContent.trim(),
        });

      if (error) {
        throw new Error(error.message);
      }

      setPostContent("");
      setCharacterCount(0);
      setCreatePostDialogOpen(false);
      
      window.location.reload();
      
    } catch (error) {
      console.error("Error creating post:", error);
      setPostError(error instanceof Error ? error.message : "Failed to create post");
    } finally {
      setPosting(false);
    }
  };

  const handleContentChange = (e: React.ChangeEvent<HTMLTextAreaElement>): void => {
    const content = e.target.value;
    setPostContent(content);
    setCharacterCount(content.length);
  };

  const getInitials = (name: string | null | undefined): string => {
    if (!name) return "??";
    return name
      .split(" ")
      .map((part) => part[0])
      .join("")
      .toUpperCase()
      .slice(0, 2);
  };

  if (isLoading) {
    return (
      <div className="p-4 max-w-2xl mx-auto">
        <div className="mb-6">
          <Skeleton className="h-8 w-48 mb-2" />
          <Skeleton className="h-4 w-64" />
        </div>
        <div className="space-y-4">
          {[...Array(3)].map((_, i) => (
            <div key={i} className="p-4 border rounded-lg">
              <div className="flex items-center gap-3 mb-4">
                <Skeleton className="h-10 w-10 rounded-full" />
                <div className="flex-1">
                  <Skeleton className="h-4 w-32 mb-1" />
                  <Skeleton className="h-3 w-24" />
                </div>
                <Skeleton className="h-8 w-20" />
              </div>
              <Skeleton className="h-16 w-full" />
            </div>
          ))}
        </div>
      </div>
    );
  }

  if (databaseError === "database_not_setup") {
    return (
      <div className="p-4 max-w-2xl mx-auto">
        <Alert>
          <AlertCircle className="h-4 w-4" />
          <AlertTitle>Database Setup Required</AlertTitle>
          <AlertDescription>
            The database tables haven&apos;t been created yet. Please run the SQL scripts in the{" "}
            <code className="rounded bg-muted px-1 py-0.5">scripts</code> folder to set up your database.
          </AlertDescription>
        </Alert>
      </div>
    );
  }

  const isAuthenticated = !!user;

  return (
    <div className="pb-20">
      {/* Mobile Header - X/Bluesky Style */}
      <div className="sticky top-14 z-30 bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 border-b lg:hidden">
        <div className="flex items-center justify-between px-4 h-[53px]">
          {/* Left: Profile Avatar */}
          <button 
            type="button"
            className="flex items-center justify-center w-8 h-8 rounded-full overflow-hidden"
            aria-label="Open profile menu"
          >
            <Avatar className="h-8 w-8">
              <AvatarImage 
                src={profile?.avatar_url ?? undefined} 
                alt={profile?.display_name ?? "User"} 
              />
              <AvatarFallback className="text-xs">
                {getInitials(profile?.display_name)}
              </AvatarFallback>
            </Avatar>
          </button>

          {/* Center: Feed Tabs */}
          {isAuthenticated && (
            <Tabs 
              value={feedTab} 
              onValueChange={(value) => setFeedTab(value as "for-you" | "following")}
              className="flex-1 mx-4"
            >
              <TabsList className="w-full h-full bg-transparent p-0 flex items-center justify-center gap-8">
                <TabsTrigger 
                  value="for-you" 
                  className="relative bg-transparent shadow-none hover:bg-transparent data-[state=active]:bg-transparent data-[state=active]:shadow-none pb-3 px-0 font-semibold text-base after:absolute after:bottom-0 after:left-0 after:right-0 after:h-1 after:rounded-full data-[state=active]:after:bg-primary after:bg-transparent transition-colors"
                >
                  For you
                </TabsTrigger>
                <TabsTrigger 
                  value="following" 
                  className="relative bg-transparent shadow-none hover:bg-transparent data-[state=active]:bg-transparent data-[state=active]:shadow-none pb-3 px-0 font-semibold text-base after:absolute after:bottom-0 after:left-0 after:right-0 after:h-1 after:rounded-full data-[state=active]:after:bg-primary after:bg-transparent transition-colors"
                >
                  Following
                </TabsTrigger>
              </TabsList>
            </Tabs>
          )}

          {!isAuthenticated && (
            <div className="flex-1 text-center">
              <h1 className="text-lg font-bold">Explore</h1>
            </div>
          )}

          {/* Right: Settings/Filter Button */}
          {isAuthenticated && (
            <Sheet>
              <SheetTrigger asChild>
                <Button 
                  variant="ghost" 
                  size="icon" 
                  className="h-8 w-8"
                  aria-label="Feed settings"
                >
                  <Sparkles className="h-5 w-5" />
                </Button>
              </SheetTrigger>
              <SheetContent side="right">
                <SheetHeader>
                  <SheetTitle>Feed Settings</SheetTitle>
                </SheetHeader>
                <div className="mt-6 space-y-4">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      <Users className="h-4 w-4" />
                      <span className="text-sm">Following: {followingUserIds.length - 1}</span>
                    </div>
                  </div>
                  <div className="flex items-center justify-between">
                    <Label htmlFor="mobile-follow-buttons" className="text-sm">
                      Show follow buttons
                    </Label>
                    <Switch
                      id="mobile-follow-buttons"
                      checked={showFollowButtons}
                      onCheckedChange={setShowFollowButtons}
                    />
                  </div>
                </div>
              </SheetContent>
            </Sheet>
          )}
        </div>
      </div>

      {/* Desktop Header */}
      <div className="hidden lg:block max-w-2xl mx-auto p-4">
        <div className="mb-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold">
                {isAuthenticated ? "Home" : "Explore Posts"}
              </h1>
              <p className="text-muted-foreground mt-2">
                {isAuthenticated 
                  ? "Your personalized feed" 
                  : "Sign in to see posts from people you follow"}
              </p>
            </div>
            
            {isAuthenticated && (
              <div className="flex items-center gap-4">
                <Dialog open={createPostDialogOpen} onOpenChange={setCreatePostDialogOpen}>
                  <DialogTrigger asChild>
                    <Button 
                      className="bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700"
                    >
                      <Pencil className="mr-2 h-4 w-4" />
                      Create Post
                    </Button>
                  </DialogTrigger>
                  <DialogContent className="sm:max-w-md">
                    <DialogHeader>
                      <DialogTitle className="flex items-center gap-2">
                        <Pencil className="h-5 w-5" />
                        Create Post
                      </DialogTitle>
                    </DialogHeader>
                    <div className="space-y-4 py-4">
                      <div className="flex items-start gap-3">
                        <Avatar className="h-10 w-10">
                          <AvatarImage 
                            src={profile?.avatar_url ?? undefined} 
                            alt={profile?.display_name ?? "User"} 
                          />
                          <AvatarFallback>
                            {getInitials(profile?.display_name)}
                          </AvatarFallback>
                        </Avatar>
                        <div className="flex-1">
                          <Textarea
                            placeholder="What's happening?"
                            value={postContent}
                            onChange={handleContentChange}
                            className="min-h-[120px] resize-none border-0 p-0 focus-visible:ring-0 text-lg"
                            maxLength={280}
                            disabled={posting}
                          />
                        </div>
                      </div>
                      
                      <div className="flex items-center justify-between">
                        <div className="text-sm text-muted-foreground">
                          {characterCount}/280 characters
                          {characterCount > 250 && (
                            <span className="ml-2 text-orange-500">
                              {280 - characterCount} left
                            </span>
                          )}
                        </div>
                        <Badge 
                          variant={characterCount > 280 ? "destructive" : "secondary"}
                          className={characterCount > 250 ? "bg-orange-500" : ""}
                        >
                          {characterCount > 280 ? "Too long!" : "Good"}
                        </Badge>
                      </div>
                      
                      {postError && (
                        <Alert variant="destructive">
                          <AlertCircle className="h-4 w-4" />
                          <AlertDescription>{postError}</AlertDescription>
                        </Alert>
                      )}
                      
                      <div className="flex justify-end gap-2">
                        <Button
                          variant="outline"
                          onClick={() => setCreatePostDialogOpen(false)}
                          disabled={posting}
                        >
                          Cancel
                        </Button>
                        <Button
                          onClick={() => void handleCreatePost()}
                          disabled={posting || !postContent.trim() || characterCount > 280}
                          className="bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700"
                        >
                          {posting ? (
                            <>
                              <span className="animate-spin mr-2">⟳</span>
                              Posting...
                            </>
                          ) : (
                            "Post"
                          )}
                        </Button>
                      </div>
                    </div>
                  </DialogContent>
                </Dialog>
                
                <div className="flex items-center gap-2">
                  <Switch
                    id="follow-buttons"
                    checked={showFollowButtons}
                    onCheckedChange={setShowFollowButtons}
                  />
                  <Label htmlFor="follow-buttons" className="text-sm">
                    Show follow buttons
                  </Label>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Feed */}
      <div className="max-w-2xl mx-auto lg:p-0 px-4">
        {isAuthenticated && (
          <>
            {/* Desktop Create Post */}
            <div className="hidden lg:block mb-6">
              <div className="rounded-xl border bg-card p-6">
                <div className="flex items-start gap-4">
                  <Avatar className="h-12 w-12">
                    <AvatarImage 
                      src={profile?.avatar_url ?? undefined} 
                      alt={profile?.display_name ?? "User"} 
                    />
                    <AvatarFallback>
                      {getInitials(profile?.display_name)}
                    </AvatarFallback>
                  </Avatar>
                  <div className="flex-1">
                    <Dialog open={createPostDialogOpen} onOpenChange={setCreatePostDialogOpen}>
                      <DialogTrigger asChild>
                        <Button 
                          variant="outline" 
                          className="w-full justify-start text-muted-foreground hover:text-foreground h-12 text-left font-normal"
                        >
                          <span className="truncate">What&apos;s on your mind?</span>
                        </Button>
                      </DialogTrigger>
                    </Dialog>
                    <div className="mt-4 flex items-center justify-between">
                      <div className="flex gap-2">
                        <Button variant="ghost" size="sm" className="gap-2">
                          <span className="text-blue-500">📷</span>
                          <span className="hidden sm:inline">Photo</span>
                        </Button>
                        <Button variant="ghost" size="sm" className="gap-2">
                          <span className="text-green-500">🎥</span>
                          <span className="hidden sm:inline">Video</span>
                        </Button>
                        <Button variant="ghost" size="sm" className="gap-2">
                          <span className="text-purple-500">📊</span>
                          <span className="hidden sm:inline">Poll</span>
                        </Button>
                      </div>
                      <Dialog open={createPostDialogOpen} onOpenChange={setCreatePostDialogOpen}>
                        <DialogTrigger asChild>
                          <Button 
                            size="sm"
                            className="bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700"
                          >
                            <Pencil className="h-4 w-4 mr-2" />
                            Post
                          </Button>
                        </DialogTrigger>
                      </Dialog>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </>
        )}

        {/* Feed component with conditional filtering */}
        <Feed 
          userId={user?.id}
          followingUserIds={feedTab === "following" ? followingUserIds : []}
          isAuthenticated={isAuthenticated}
          showFollowButton={showFollowButtons}
          onFollowChange={handleFollowChange}
        />

        {/* Mobile Create Post - At the bottom - Only show on "For you" tab */}
        {isAuthenticated && feedTab === "for-you" && (
          <div className="lg:hidden mt-6 mb-6 rounded-xl border bg-card p-4">
            <div className="flex items-center gap-3">
              <Avatar className="h-10 w-10">
                <AvatarImage 
                  src={profile?.avatar_url ?? undefined} 
                  alt={profile?.display_name ?? "User"} 
                />
                <AvatarFallback>
                  {getInitials(profile?.display_name)}
                </AvatarFallback>
              </Avatar>
              <Dialog open={createPostDialogOpen} onOpenChange={setCreatePostDialogOpen}>
                <DialogTrigger asChild>
                  <Button 
                    variant="outline" 
                    className="flex-1 justify-start text-muted-foreground hover:text-foreground h-10 text-left font-normal"
                  >
                    <span className="truncate">Start a post...</span>
                  </Button>
                </DialogTrigger>
              </Dialog>
            </div>
          </div>
        )}
      </div>

      {/* Floating Action Button for Mobile */}
      {isAuthenticated && (
        <div className="fixed bottom-20 right-4 z-40 lg:hidden">
          <Dialog open={createPostDialogOpen} onOpenChange={setCreatePostDialogOpen}>
            <DialogTrigger asChild>
              <Button 
                size="icon"
                className="h-14 w-14 rounded-full shadow-lg bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700"
              >
                <Plus className="h-6 w-6" />
              </Button>
            </DialogTrigger>
          </Dialog>
        </div>
      )}
    </div>
  );
}